@import "./colors";

*:after,
*:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: "Assistant";
  background: $primary;

  &.dark {
    background: black;
    a {
      color: white;
    }
  }

  &.light {
    a {
      color: black;
    }
  }
}

img {
  object-fit: contain;
}

#main-paper {
  height: auto;
  box-shadow: none;
}

.warm {
  background: linear-gradient(to top right, $warm1 0%, $warm2 100%);
}

.mid {
  background: linear-gradient(to top right, $mid1 0%, $mid2 100%);
}

.cold {
  background: #abbaab; /* fallback for old browsers */
  background: linear-gradient(
    to right,
    $cold1,
    $cold2
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.ice {
  background: linear-gradient(
    to right,
    $ice1,
    $ice2
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.warm-text {
  text-transform: uppercase;

  font-weight: bold;
}

.MuiAutocomplete-popper {
  z-index: 1 !important;

  width: 100%;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
