@import "../../styles/colors";
.navbar {
  padding: 1rem 0;
  position: sticky;
  min-height: 10vh;
  top: 0;
  z-index: 2;
  & > .items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }

  ul {
    display: flex;

    justify-content: center;
    align-items: center;

    li {
      padding: 0.25rem 0.75rem;
      border-radius: 25px;

      a {
        padding-bottom: 0.5rem;
        border-bottom: 3px transparent solid;

        transition: border-bottom 0.2s linear;
        &:hover {
          border-color: black;
        }
      }
    }
  }

  &.dark-theme {
    .logo path {
      fill: white;
    }
    button {
      background-color: white;
      color: black;
    }
    li:not(:last-child) {
      a {
        color: white;

        &.active {
          border-bottom-color: white;
        }
      }
    }
  }
  &.light-theme {
    button {
      background-color: rgba(201, 201, 201, 0.186);
      color: black;
    }
    li:not(:last-child) {
      color: var(--light);
      a {
        color: black;
        &.active {
          border-bottom-color: black;
        }
      }
    }
  }
}

.logo {
  height: calc(40vw - 80px);
  max-height: 70px;
}

@keyframes toggleMode {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media (max-width: 705px) {
  .navbar {
    .items {
      justify-content: center;
    }
    .logo {
      text-align: center;
    }
  }
}

@media (max-width: 400px) {
  ul {
    flex-direction: column;

    li:last-child {
      position: absolute;
      right: 0;
    }
  }
}
